"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";

export default function AdminDashboard() {
    const [stats, setStats] = useState({ total: 0, exclusive: 0 });
    const [leads, setLeads] = useState<any[]>([]);

    useEffect(() => {
        const fetchData = async () => {
            const { data } = await supabase.from('leads').select('*').order('created_at', { ascending: false });
            if (data) {
                setLeads(data);
                setStats({
                    total: data.length,
                    exclusive: data.filter(l => l.status === 'sold_exclusive').length
                });
            }
        };
        fetchData();
    }, []);

    return (
        <div className="space-y-8">
            <h1 className="text-3xl font-bold">Dashboard Overview</h1>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="glass p-6 rounded-xl border border-white/10">
                    <div className="text-sm text-muted-foreground uppercase tracking-wider">Total Leads</div>
                    <div className="text-4xl font-bold mt-2">{stats.total}</div>
                </div>
                <div className="glass p-6 rounded-xl border border-white/10">
                    <div className="text-sm text-muted-foreground uppercase tracking-wider">Exclusive Sales</div>
                    <div className="text-4xl font-bold mt-2 text-primary">{stats.exclusive}</div>
                </div>
                <div className="glass p-6 rounded-xl border border-white/10">
                    <div className="text-sm text-muted-foreground uppercase tracking-wider">Revenue (Est)</div>
                    <div className="text-4xl font-bold mt-2">€0</div>
                </div>
            </div>

            {/* Recent Leads Table */}
            <div className="glass rounded-xl border border-white/10 overflow-hidden">
                <div className="p-6 border-b border-white/10">
                    <h3 className="font-semibold text-lg">Recent Inventory</h3>
                </div>
                <div className="overflow-x-auto">
                    <table className="w-full text-sm text-left">
                        <thead className="bg-white/5 uppercase text-xs text-muted-foreground font-medium">
                            <tr>
                                <th className="px-6 py-4">Title</th>
                                <th className="px-6 py-4">Brand</th>
                                <th className="px-6 py-4">Status</th>
                                <th className="px-6 py-4">Price (Std/Excl)</th>
                                <th className="px-6 py-4">Date</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-white/5">
                            {leads.map((lead) => (
                                <tr key={lead.id} className="hover:bg-white/5 transition-colors">
                                    <td className="px-6 py-4 font-medium">{lead.title}</td>
                                    <td className="px-6 py-4 text-muted-foreground">{lead.brand}</td>
                                    <td className="px-6 py-4">
                                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${lead.status === 'available' ? 'bg-green-500/10 text-green-500' : 'bg-primary/10 text-primary'
                                            }`}>
                                            {lead.status}
                                        </span>
                                    </td>
                                    <td className="px-6 py-4">€{lead.price_standard} / €{lead.price_exclusive}</td>
                                    <td className="px-6 py-4 text-muted-foreground">{new Date(lead.created_at).toLocaleDateString()}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                    {leads.length === 0 && (
                        <div className="p-8 text-center text-muted-foreground">No leads found.</div>
                    )}
                </div>
            </div>
        </div>
    );
}
